import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;
let {
	log
} = console;
export default {
	props: ['ware', 'config', 'url'],
	data() {
		return {
			imgRemote: imgRemote,
			token: uni.getStorageSync('cdj_token')
		}
	},
	methods: {
		showCart() {
			this.$emit('showCart')
		},
		addcart(url, num, message = '成功加入购物车') {
						let item = this.ware;
						let timeStamp = Math.round(new Date().getTime() / 1000);
						let obj = {
							appid: appid,
							timeStamp: timeStamp,
							item_id: item.id,
							attr_id: 0,
							item_num: num
						};
						let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						let params = Object.assign({
								sign: sign
							},
							obj
						);
						rs.postRequests(url, params, res => { 
							let data = res.data;
							if (data.code == 200) {
								uni.showToast({
									title:message,
									icon:'none',
									duration:2000
								})
								if (num <= 0) {
									this.ware.cart_num = 0;
								} else {
									this.ware.cart_num = num;
								}
							} else if (data.code == 407 || data.code == 406) {
								Toast("购买数量不能超过活动数量");
								console.log(this.shopList.cart_num)
								console.log(item)
								this.ware.cart_num = item.activity_num;
							} else {
								Toast(res.data.msg);
							}
						});
					},
		minus(e){
			if(e==0){
					this.addcart('deleteCart', e, '成功删除商品');
			}else{
				this.addcart('changeNum', e);
			}
		},
		plus(e){
		this.addcart('changeNum', e);
		},
		plusCart(){
			this.addcart('changeNum', 1);
		},
		showKey() {
			this.$emit('showKey')
		}
	}
}
